
import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { CreateComponent } from './create/create.component';
import { ListComponent } from './list/list.component';
import { ActionDialogComponent } from './action-dialog/action-dialog.component';
import { BookingRoutingModule } from './booking-routing.module';
import { BookingCrudComponent } from './booking-crud/booking-crud.component';
import { FuseSharedModule  } from '@fuse/shared.module';
import { ViewDialogComponent } from './view-dialog/view-dialog.component';
import {MatStepperModule} from '@angular/material/stepper';
import { RegisterUserDialogComponent } from './registerUser-dialog/registerUser-dialog.component';
import { ListApprovedComponent } from './list-approved/list-approved.component';
import { RegisterVehicleDialogComponent } from './registerVehicle-dialog/registerVehicle-dialog.component';
const COMPONENTS =[ BookingCrudComponent, CreateComponent, ListComponent,ListApprovedComponent];
const COMPONENTS_DYNAMIC =[ViewDialogComponent,ActionDialogComponent,RegisterUserDialogComponent,RegisterVehicleDialogComponent];

@NgModule({
    imports:[SharedModule, BookingRoutingModule,FuseSharedModule,MatStepperModule],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC],
    entryComponents : [COMPONENTS_DYNAMIC]
})

export class BookingModule{}