import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn,AbstractControl, FormControl} from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { MasterService } from 'app/shared/services/master/master.service';
import { VehicleService } from 'app/shared/services/master/vehicle/vehicle.service';
import { Router } from '@angular/router';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Subject } from 'rxjs';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { FuseConfigService } from '@fuse/services/config.service';
import { AccountService } from 'app/shared/services/account/account.service';
import { MessageConstant } from 'app/shared/utils/message.constant';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}
@Component({
  selector: 'app-registerUser-dialog',
  templateUrl: './registerUser-dialog.component.html',
  styleUrls: ['./registerUser-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class RegisterUserDialogComponent implements OnInit {

  
  registerForm: FormGroup;
  isPasswordFieldType: boolean;
  isPasswordConfirmFieldType: boolean;
  logs:any;
  validateOTP:boolean= false;
  hideDetail:boolean= true;
  // Private
  isErrorMessage = false;
  errorMessage = "";
  isSuccessMessage = false;
  successMessage ="";
  private _unsubscribeAll: Subject<any>;

  form: FormGroup;
  brands:any;
  models:any;
  connector_types:any;
  localStorage : any;
  filteredOptionsbrands: Observable<any[]>;
  filteredOptionsModel: Observable<any[]>;
  filteredOptionsCurrType: Observable<any[]>;
  @ViewChild("toggleElement") ref: any;
  status = false;
  errorMsg:String;
  splitted:String;

  otpForm: FormGroup;
  selectedIndex = 0;
  isFirstTab = true;
  isSecondTab = true;
 
  constructor(
    public dialogRef: MatDialogRef<RegisterUserDialogComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, 
    private accountService: AccountService,
     private _formBuilder: FormBuilder,private router : Router,private masterService : MasterService,
     private vehicleService : VehicleService,private snackBar: MatSnackBar) {}

  ngOnInit() {
    this.registerForm = this._formBuilder.group({
      f_name: ['', [Validators.required]],
      l_name: ['', [Validators.required]],
      username: ['', [Validators.required]],
      email: ['', [Validators.required]],
      MobNo: ['', [Validators.required, Validators.pattern('^((\\+91-?)|0)?[0-9]{10}$')]],
      u_altmobile: ['',],
      password: ['', [Validators.required]],
      confirmPassword: ['', [this.confirmValidator]],
    });

    this.otpForm = this._formBuilder.group({
      otp :['']
    });

    this.initialize();
  }
  initialize(){
    ////

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    
    this.form = this._formBuilder.group({
      brand: ['', [autocompleteObjectValidator(),Validators.required]],
      model: ['', [autocompleteObjectValidator(),Validators.required]],
      connector_type : ['', [autocompleteObjectValidator(),Validators.required]],
      registration: ['', [Validators.required,
        Validators.minLength(8),Validators.maxLength(100),
        Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      yearOfManufacture: ['', [Validators.required]],
      engineNumber: ['', [ Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      chassisNumber: ['', [ Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      VINnumber: [''],
      status : this.status == true ? 'Y' : 'N'   
      // vehicle: ['', [Validators.required]],
      // manufacturer: ['', [Validators.required]],
      
    });

    this.getBrands();
  }
  closeActionPanel(){
    this.dialogRef.close();
  }
  confirmValidator = (control: FormControl): { [s: string]: boolean } => {
    if (!control.value) {
      return { error: true, required: true };
    } else if (control.value !== this.registerForm.controls.password.value) {
      return { error: true, confirm: true };
    }
  }
  togglePasswordFieldType(){
    this.isPasswordFieldType = !this.isPasswordFieldType;
  }
  togglePasswordConfirmFieldType(){
    this.isPasswordConfirmFieldType = !this.isPasswordConfirmFieldType;
  }
  public resolved(captchaResponse: string) {
  }
  register(){
    this.selectedIndex = 1;
    let formValues = this.registerForm.value;
    let params = {
      f_name:formValues.f_name, 
      l_name:formValues.l_name,
      username : formValues.username ,
      password : formValues.confirmPassword ,
      mobile :   formValues.MobNo ,
      alt_mobile : "1234567890" ,
      email : formValues.email,
      device_id : "qwertyuiop",
      app_version : "9876543wsdfghnm",
      os_version : "10",
      user_type : "END" ,
      can_expire :"Y",
      is_verified : "N" ,
      registration_origin : "WEB" ,
      status : "Y" ,
      created_by   : 1
     
    }
     //
      this.accountService.register(params).subscribe((res:any)=>{
        //
       
        if(res.status == true){
          this.successMessage = MessageConstant.userOTPSend;
          this.isSuccessMessage = true;
          this.isFirstTab = false;
          setTimeout(() => {                           //<<<---using ()=> syntax
          this.isSuccessMessage = false;
        }, 3000);
          //this.snackBar.open(MessageConstant.userOTPSend, 'Dismiss', { duration: 3000 });
          this.hideDetail = false;
          this.validateOTP = true;
        }else{
          this.errorMessage = MessageConstant.dublicateRegistrationMessage;
          this.isErrorMessage = true;
          setTimeout(() => {                           //<<<---using ()=> syntax
          this.isErrorMessage = false;
        }, 3000);
          //this.snackBar.open(MessageConstant.dublicateRegistrationMessage, 'Dismiss', { duration: 3000 });
        }
        
       },(err)=>{
        this.errorMessage = MessageConstant.errorMessage;
        this.isErrorMessage = true;
        setTimeout(() => {                           //<<<---using ()=> syntax
        this.isErrorMessage = false;
      }, 3000);
        //this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
       this.router.navigateByUrl('/register');   
    })
  }
  vaidateOTP(){
    let formValues = this.otpForm.value;
    let params = {
      username : formValues.username ,
      mobile :   formValues.MobNo ,
      email : formValues.email,
      otp: formValues.otp
     
    }
    this.accountService.verifyUserNew(params).subscribe((res:any)=>{
      this.logs = res;
      //this.router.navigateByUrl('/login'); 
      this.successMessage = MessageConstant.userRegisterdMessage;
      this.isSuccessMessage = true;
      this.isSecondTab = false;
      setTimeout(() => {                           //<<<---using ()=> syntax
      this.isSuccessMessage = false;
    }, 3000);
        //this.snackBar.open(MessageConstant.userRegisterdMessage, 'Dismiss', { duration: 3000 });
     },(err)=>{
      this.errorMessage = MessageConstant.errorMessage;
      this.isErrorMessage = true;
      setTimeout(() => {                           //<<<---using ()=> syntax
      this.isErrorMessage = false;
    }, 3000);
      //this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
     this.router.navigateByUrl('/register');   
  })
  }
  resendOTP(){
    let formValues = this.otpForm.value;
    let params = {
      mobile :   formValues.MobNo ,
      email : formValues.email,
      username:formValues.username,
      id:null,
      otp_purpose : "REGISTER" 
    }
    this.accountService.getotpNew(params).subscribe((res:any)=>{
      this.logs = res;
      this.successMessage = MessageConstant.getOTPMessage;
      this.isSuccessMessage = true;
      setTimeout(() => {                           //<<<---using ()=> syntax
      this.isSuccessMessage = false;
      }, 3000);
        //this.snackBar.open(MessageConstant.getOTPMessage, 'Dismiss', { duration: 3000 });
     },(err)=>{
      this.errorMessage = MessageConstant.errorMessage;
      this.isErrorMessage = true;
      setTimeout(() => {                           //<<<---using ()=> syntax
      this.isErrorMessage = false;
    }, 3000);
      //this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
      this.router.navigateByUrl('/register');   
  })
  }

  getBrands(){
    this.masterService.getBrands().subscribe(
      (data:any)=>{
        
        this.brands = data;
        
        this.filteredOptionsbrands = this.form.controls.brand.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterbrands(name) : this.brands)
        );
      },
      (err)=>{
        ////
      })
  }
  getModelsByBrand(id){
    this.models=[];
    this.connector_types=[];
    this.masterService.getModelsByBrand(id).subscribe(
      (data:any)=>{
        this.models = data;
        this.filteredOptionsModel = this.form.controls.model.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterModels(name) : this.models)
        );
      },
      (err)=>{
        ////
      })
  }
  getConnectorTypeByModel(id){
    this.connector_types=[];
    this.masterService.getPublishedConnectorTypeByModel(id).subscribe(
      (data:any)=>{
        this.connector_types = data;
        this.filteredOptionsCurrType = this.form.controls.connector_type.valueChanges
        .pipe(
          startWith(''),
          map(value => typeof value === 'string' ? value : value.name),
          map(name => name ? this._filterConnType(name) : this.connector_types)
        );
      },
      (err)=>{
        ////
      })
  }
  onBrandChange(event){
    this.getModelsByBrand(event);
    this.form.get('model').setValue('');
  }
  onModelChange(event){
    this.getConnectorTypeByModel(event);
  }
  onConnectorTypeChange(event){
    ////
    // this.getConnectorTypeByModel(event.value);
  }

  onSubmit(){
    if(this.form.valid){
      let formValues = this.form.value;
    

      let params = {
          brand_id : formValues.brand.id,
          model_id : formValues.model.id,
          connector_type_id : formValues.connector_type.ct_id,
          registration_no : formValues.registration,
          year_of_manufacture : formValues.yearOfManufacture,
          engine_no : formValues.engineNumber,
          chassis_no : formValues.chassisNumber,
          vin_no : formValues.VINnumber,
          status :  this.status == true ? 'Y' : 'N', 
          created_by : this.localStorage.userId
      }
      this.vehicleService.register(params).subscribe(
        (resp:any )=>{
          ////
          if(resp.status == true){
            this.snackBar.open(MessageConstant.vehicleCreate, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/booking/crud/create']);
          }else{
            if(resp.err_code == "ERROR : ER_DUP_ENTRY"){
              this. errorMsg = resp.message;
              var index=  this. errorMsg.lastIndexOf(" ");
              
              this.splitted = this. errorMsg.substring(index+1); 
              if(this.splitted == "'registration_no'" ){
                this.snackBar.open(MessageConstant.registrationDuplicateVehicle, 'Dismiss', { duration: 5000 });
              } if(this.splitted == "'engine_no'" ){
                this.snackBar.open(MessageConstant.engineDuplicateVehicle, 'Dismiss', { duration: 5000 });
              }if(this.splitted == "'chassis_no'" ){
                this.snackBar.open(MessageConstant.chassisDuplicateVehicle, 'Dismiss', { duration: 5000 });
              }

            }else{
              this.snackBar.open(resp.message, 'Dismiss', { duration: 5000 });
            }
            //this.snackBar.open(MessageConstant.errorMessage,'Dismiss', { duration: 5000 })
          }
  
        },
        (err)=>{
          ////
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        }
      )

    }
    else{
      return;
    }
  }
  onTabChange(event) {
    this.selectedIndex = event.index;
  }
  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }
  displayBrands(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayModels(item: any): string {
    return item && item.name ? item.name : '';
  }
  displayCurrentType(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterbrands(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.brands.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.models.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  private _filterConnType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.connector_types.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  updateStatus(event){
    this.status = event.checked;
    this.ref.checked = this.status;
  }

}