
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BookingCrudComponent } from './booking-crud/booking-crud.component';
import { TABS_DEMO_ROUTES } from './booking-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : BookingCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    },
    {
        path : 'bookingStationConfig',
        loadChildren: () => import('app/main/booking/booking-config/booking-config.module').then(m => m.BookingConfigModule)
    },
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class BookingRoutingModule{}